$accent: #5da68b;
$grey: #4C4C4C;
$light: #9a9a9a;
$basic: #f6f9f3;

*,
*::after,
*::before {
  box-sizing: border-box; }

a {
    text-decoration: none;
    color: inherit;
    display: inline-block; }
ul, li {
    margin: 0;
    padding: 0;
    list-style: none; }
h1 {
    font-family: 'Deutschlander', sans-serif;
    text-transform: capitalize;
    font-weight: 400;
    font-style: normal;
    font-size: 5.25rem;
    font-display: swap;
    letter-spacing: 0.04em; }

h2 {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-size: 1.125rem;
    font-style: normal;
    font-display: swap;
    font-weight: 600;
    letter-spacing: .14em; }

h3, h4, h5, h6 {
    font-weight: 700;
    line-height: 1.5rem;
    letter-spacing: .04em; }
p {
    margin: 0;
    font-weight: 700;
    letter-spacing: .04em;
    text-transform: uppercase;
    font-style: normal;
    font-display: swap;
    font-weight: 600; }

body {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-size: 1rem;
    font-style: normal;
    font-display: swap;
    font-weight: 400;
    line-height: 1.5rem; }

.form_btn {
    padding: 1.5rem 3.5rem;
    border: none;
    outline: none;
    color: #fff;
    text-transform: uppercase;
    font-size: 0.875rem;
    font-style: normal;
    font-display: swap;
    font-weight: 700;
    letter-spacing: .04em;
    background-color: $accent;
    cursor: pointer; }



/** _______________________________START________________________________ **/

.container {
    max-width: 1200px;
    padding-left: 1rem;
    padding-right: 1rem;
    margin: 0 auto; }
.container-fluid {
    max-width: 1720px;
    margin: 0 auto;
    overflow: hidden; }


///////////////////////////////////
///// HEADER
//////////////////////////////////
.header, .footer {
    &__inner {
        display: flex;
        justify-content: space-between;
        height: 6rem;
        align-items: center; }
    &__text {
        margin-right: auto;
        margin-left: 2rem;
        font-size: .8rem;
        font-weight: 600;
        color: $light;
        position: relative;
        margin-top: 1.75rem;

        &:after {
            content: '';
            background-image: url('../img/icons/canada.svg');
            width: 1rem;
            height: 1rem;
            position: absolute;
            right: 49px;
            top: -8px; } }
    &__phone {
        font-weight: 600;
        font-size: 1.1rem;
        letter-spacing: 0.11em;
        margin-top: .5rem; }
    &__phone a {
        color: $grey;
        text-decoration: none;
        position: relative;
        &:before {
            content: '';
            background-image: url(../img/phone.svg);
            width: 15px;
            height: 15px;
            position: absolute;
            left: -1.5rem;
            top: 5px; } }
    &__phone-text {
        font-weight: 600;
        font-size: .8rem;
        color: $light;
        text-align: right;
        padding-top: 0;
        letter-spacing: .1em; } }
.footer__text {
    &:after {
        content: '';
        background-image: none; } }



///////////////////////////////////
///// HERO SECTION
//////////////////////////////////

.top {


    margin-bottom: 4.5rem;
    &__images {
        background-image: url('../img/top-bg1.jpg');
        min-height: 34rem;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        width: 1720px;
        margin: 0 auto; }
    &__wrapper {
        margin-top: -28rem; }
    &__inner {
        display: flex;
        justify-content: space-between;
        &-img {
            margin-left: 7.75rem;
            drop-shadow: (0px -4px 22px rgba(0,  0, 0, 0.4)); } }
    &__inner-form {
        max-width: 36rem;
        width: 100%; }

    &__inner-suptitle {
        margin-top: 1rem;
        margin-bottom: 2rem;
        color: #fff; }
    &__inner-title {
        margin-bottom: 3rem;
        color: #fff;
        line-height: 5rem; }
    &__inner-text {
        width: 30rem;
        font-weight: 500;
        margin-bottom: 3rem;
        line-height: 1.75rem;
        color: #fff; }

    &-form {
        background-color: #fff;
        padding: 1.5rem;

        &__title {
            width: 16rem;
            margin-bottom: 2rem; } }

    &-form form {
        border: 1px solid #c8dcba;
        position: relative; }


    &-form__inner {
        padding: 3rem 3rem 7.5rem 3rem; }

    &-form input {
        border: none;
        padding-top: 1rem;
        padding-bottom: 1rem;
        border-bottom: 1px solid $accent;
        outline: none;
        font-size: 1rem;
        width: 12rem; }
    &-form input + input {
        margin-left: 1.5rem; }
    &-form input::placeholder {
        color: $accent; }
    &-form button {
        position: absolute;
        right: -1px;
        bottom: 0; } }


///////////////////////////////////
///// 5 advantages
//////////////////////////////////
.advantages {
    margin-bottom: 8rem;
    &__inner {
        display: flex;
        justify-content: space-around; }
    &__item {
        display: flex;
        flex-direction: column;
        width: 14rem;
        &-icon {
            text-align: center; }

        &-text {
            padding-top: 1.5rem;
            text-align: center; } } }

///////////////////////////////////
/////     05 - Main Features
//////////////////////////////////
.list {
    top: 5px;
    &__title {
        max-width: 38rem;
        margin-bottom: 4rem;
        &-suptitle {
            margin-bottom: 2rem;
            & h2 {
                font-weight: 700; } } }
    &__inner {
        display: flex;
        justify-content: space-between;
        &-features {
            margin-top: 3rem;
            background-color: $basic;
            position: relative;
            &::before {
                content: '';
                background-color: $basic;
                position: absolute;
                height: 100%;
                top: 0;
                width: 100%;
                right: 100%; }
            &::after {
                content: '';
                background-color: $basic;
                position: absolute;
                height: 100%;
                top: 0;
                width: 100%;
                left: 100%; } } } }


.tabs {
    padding-right: 1.5rem;
    width: 100%;
    &__features {
        padding-top: 1rem; }
    &__content {
        min-width: 32rem;
        height: 44rem;
        background-image: url(../img/list-images.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top center;
        position: relative;
        margin-top: -6rem; }
    &__box {
        display: flex;
        flex-wrap: wrap; } }
.tab {
    max-width: {}
    font-weight: 600;
    font-size: 1rem;
    line-height: 1.5rem;
    padding: 2rem 0;
    border-bottom: 1px solid #e6efde;
    cursor: pointer;
    &-item {
        display: none;
        font-weight: 700;
        font-size: .75rem;
        color: #fff;
        width: 43px;
        height: 43px;

        border-radius: 50%;
        position: absolute;
        text-align: center;
        line-height: 25px; } }

.tab span {
  color: $accent;
  margin-right: 1rem; }

.tab-item:first-child {
  display: block; }

///////////////////////////////////
///// popularity
//////////////////////////////////

.popularity {
    padding-top: 10rem;
    padding-left: 8rem;
    margin-bottom: 2rem;
    &__right {
        margin-top: -4rem; }

    &__wrapper {
        max-width: 1720px;
        min-height: 47.25rem;
        background-image: url(../img/picture-3.jpg);
        background-repeat: no-repeat;
        padding-left: 2rem;
        margin: 2rem auto; }

    &__inner {
        display: flex;
        justify-content: flex-end; } }

.list__title {
    margin-bottom: 2rem; }
.list__title h3 {
    margin-top: 4rem;
    color: $light; }
///////////////////////////////////
/////     07 - quote
//////////////////////////////////
.quote {
    min-height: 31rem;
    background-image: url(../img/quote-bg-right.png);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: left center;
    &__wrapper {
        min-height: 31rem;
        padding: 8rem 0 10rem;
        background-image: url(../img/quote-bg-left.png);
        background-repeat: no-repeat;
        background-size: auto;
        background-position: right center; }

    &__text {
        max-width: 57rem;
        padding: 3rem 0;
        border-top: 1px solid $accent;
        border-bottom: 1px solid $accent;
        text-align: center;
        margin: 0 auto;
        & h3 {
            font-size: 1.25rem;
            line-height: 2rem; } } }
///////////////////////////////////
/////     08 -Executing Engaging Experiences
//////////////////////////////////
.items {
    &__title {
        max-width: 40rem;
        & h1 {
            margin-bottom: 3rem; }
        & h3 {
            color: $light; } }
    &__wrapper {
        background-color: $basic;
        min-height: 24rem;
        width: 100%; }
    &__inner {
        display: flex;
        justify-content: space-around;
        padding: 1.5rem;
        background-color: #fff;
        max-width: 63.5rem;
        margin: 6rem auto;
        position: relative;
        top: -4.5rem; } }
.item {
    width: 19.5rem;
    min-height: 19.5rem;
    border: 1px solid $accent;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    &__content-title {
        max-width: 14rem;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        text-transform: uppercase;
        font-size: 1.125rem;
        line-height: 1.5rem;
        font-style: normal;
        font-display: swap;
        font-weight: 700;
        letter-spacing: .02em;
        padding: -2rem 1rem;
        margin-top: 5rem; }
    &__number {
        font-family: 'Montserrat', sans-serif;
        font-size: 5.5rem;
        line-height: 5.5rem;
        font-weight: 900;
        letter-spacing: -.02em;
        color: $accent;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -100%); } }

///////////////////////////////////
/////     09 -composition (card)
//////////////////////////////////
.composition {
    padding-top: 8rem;
    padding-bottom: 4rem;
    &__item {
        background-color: $accent;
        margin-bottom: 1.75rem;
        position: relative;
        &-image {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0.8;
            filter: sepia(15%); }



        &-inner {
            width: 17rem;
            height: 17rem;
            background-repeat: no-repeat;
            background-size: auto;
            background-position: center;
            &:hover .composition__item-effect {
                opacity: 1; } }

        &-text {
            font-family: 'Montserrat', sans-serif;
            text-transform: uppercase;
            font-size: 1.125rem;
            font-weight: 500;
            letter-spacing: .09em;
            line-height: 2.25rem;
            color: #fff; }
        &-num {
            font-family: 'Montserrat', sans-serif;
            text-transform: uppercase;
            font-size: 1.125rem;
            font-weight: 700;
            letter-spacing: .09em;
            color: #fff;
            top: 1rem;
            right: .5rem;
            position: absolute;
            z-index: 1; }
        &-name {
            position: absolute;
            font-family: 'Montserrat', sans-serif;
            text-transform: uppercase;
            font-size: 1.125rem;
            font-weight: 700;
            letter-spacing: .09em;
            color: #fff;
            transform: rotate(-90deg);
            bottom: 5.5rem;
            left: -3.5rem;
            z-index: 1; }
        &-effect {
            opacity: 0;
            position: absolute;
            left: 1rem;
            right: 1rem;
            top: 1rem;
            bottom: 1rem;
            display: flex;
            justify-content: center;
            align-content: center;
            background-image: url(../img/installations/comp-bg.png);

            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
            transition: all .4s;
            z-index: 2; }
        &-text {
            font-family: 'Montserrat', sans-serif;
            text-transform: uppercase;
            padding: 5rem .5rem;
            font-size: 1rem;
            line-height: 1.5rem;
            text-align: center;
            font-weight: 500;
            letter-spacing: .02em;
            color: #fff; } }
    &__row {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; }
    &__box {
        max-width: 35.75rem;
        padding-left: 2rem; }

    &__title h1 {
        margin-top: 1rem;
        margin-bottom: 4rem; }
    &__title h3 {
        max-width: 34.25rem;
        line-height: 1.75rem; }
    &__link {
        max-width: 52.125rem;
        height: 100%;
        padding: 2rem 8rem 3.9rem 3.5rem;
        border: 1px solid $accent;
        position: relative;
        &-text {
            line-height: 1.75rem;
            margin-bottom: 1rem;
            & h3 {
                padding: 1rem; } }
        &-btn {
            position: absolute;
            padding: 1.5rem 3.5rem;
            border: none;
            outline: none;
            color: #fff;
            text-transform: uppercase;
            font-size: 0.875rem;
            font-style: normal;
            font-display: swap;
            font-weight: 700;
            letter-spacing: .04em;
            background-color: #5da68b;
            cursor: pointer;
            position: absolute;
            right: -1px;
            bottom: 0; } } }

///////////////////////////////////
/////     ACORDION
//////////////////////////////////
/* (A) TABS CONTAINER */
.faq {
    position: relative;
    max-width: 600px;
    border-bottom: none;
    margin: 1.25rem 0;
    & input {
      display: none; }
    &  label {
        display: block;
        margin-top: 1rem;
        padding: 1.5rem;
        font-size: 1rem;
        color: $grey;
        font-style: normal;
        text-transform: none;
        font-weight: 400;
        background: $basic;
        cursor: pointer; }
    & .faq-content {
        background: #fff;
        overflow: hidden;
        transition: max-height 0.3s;
        max-height: 0;
        & p {
            padding: 1rem;
            color: $light;
            font-style: normal;
            text-transform: none;
            font-weight: 400; } }
    & input:checked ~ .faq-content {
        max-height: 100vh; }
    & label::after {
        content: "\1F862";
        position: absolute;
        right: 1rem;
        top: 1.5rem;
        display: block;
        transition: all .4s; }
    & input:checked ~ label::after {
        transform: rotate(90deg); } }

///////////////////////////////////
/////     built FAQ
//////////////////////////////////
.build {
    &__wrapper {
        padding: 4rem 0 4rem;
        background-image: url(../img/faq_bg.jpg);
        background-position: left center;
        background-repeat: no-repeat;
        min-height: 48rem; }

    &__content {
        display: flex;
        justify-content: flex-end; }
    &__inner {
        max-width: 45rem;
        padding: 1.5rem;
        background-color: #fff; }
    &__inner-box {
        border: 1px solid $accent;
        padding: 4rem; }
    &__title {
        margin-bottom: 3rem; } }

///////////////////////////////////
/////     Specification
//////////////////////////////////

.spec {
    margin-top: 3rem;
    &__title {
        margin: 0 0 4rem 1.5rem; }

    &__list {
        min-height: 24rem;
        padding: 2rem 6rem;
        background-color: $basic;
        &-item {
            padding: 1.5rem 0;
            border-bottom: 1px solid #e0dede;
            display: flex;
            flex-wrap: wrap; }
        &-name {
            width: 18rem;
            margin-right: 5rem;
            text-transform: uppercase;
            font-size: 1rem;
            font-style: normal;
            font-display: swap;
            font-weight: 700;
            letter-spacing: .04em; } } }

.footer {
    margin: 1rem; }

